<div class="content"><h3 class="content__title">Профиль {{ $route.params.name }}. ID {{ $route.params.id }}.</h3><div class="faq"><h3 class="faq__title">FAQ:</h3><div class="faq__item"><h4>Once you have discord installed, clicking the second two links will add you to the Discord "Servers"</h4><p>Servers can be thought of as large overarching groups i.e. WID and Morgridge</p></div><div class="faq__item"><h4>In each server is at least one voice channel. To join a voice channel, simply click on the name</h4><p>Channels can be thought of as subgroups or departments</p><p>To disconnect from the voice channel click the icon that looks like a phone with an 'x' below the list of channels</p></div><div class="faq__item"><h4>Its recommended that you turn on push to talk for your microphone. This will make it so that you're only heard when you press a key/button</h4><p>Open settings (gear icon at the bottom of the channel list)</p><p>Click "Voice and Video" under the "App Settings" header</p><p>Click the box for "Push to Talk"</p><p>In the shortcut box, enter the key you would like to use</p><p>Note: This should be a key you don't use often. The "~" works well in Windows</p><p>Once you have the key entered, click "Stop Recording"</p><p>Exit the settings menu</p></div><div class="faq__item"><h4>By right clicking on a username, you can see advance options for that account such as:</h4><p>Adjusting the volume of that account. Useful if one person sounds to loud to you</p><p>Send a private DM</p><p>Call that person</p><p>Tag the person in a message</p></div></div></div>