<div class="content"><h3 class="content__title">Список пользователей</h3><div class="users"><div class="user" v-for="user in users" :key="user.id"> <span> <strong>Имя: {{user.name}}</strong></span><span>Айди: {{user.id}}</span><span>Никнейм: {{user.username}}</span><span>Компания: {{user.company.name}}</span><span>Город: {{user.address.city}}</span><span>Почта: <a class="user__link" href="mailto:{{user.email}}">{{user.email}}</a></span><span>Сайт: <a class="user__link" href="{{user.website}}">{{user.website}}</a></span><VButton @click="$router.push(`/user/${user.id}${user.name}`)">Профиль</VButton></div></div></div>