<div class="content"><h3 class="content__title">Это главная страница для Код Констракшн</h3><p class="content__description">Так как подробного технического задания не было, ниже некоторые пояснения по реализации задания:</p><ul class="content__todone"><li>- Сделано на Vue 3 через CLI.</li><li>- Для роутинга использовал Vue Router. Реализовано 4 страницы - Главная, Пользователи, Комментарии и на странице "Пользователи" при клике на кнопку "Профиль" можно перейти на страницу каждого Пользователя. В такие страницы передаю айди и имя пользователя, а так же в них добавлен шаблон FAQ (как было написано в задании).</li><li>- Препроцессор Pug для HTML, препроцессор SCSS для CSS.</li><li>- Наименование классов по БЭМ.</li><li>- Axios для работы с API.</li><li>- Vuex для работы с глобальными данными.</li><li>- В отдельные компоненты вынесены навигационная панель, кнопки, инпуты.</li></ul></div>